* {
  box-sizing: border-box;
}

h2{
  margin: 0px;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-item {
  list-style: none;
  display: flex;
  align-items: center;
  font-size: 20px;
  margin-top: 8px;
}

.country-flag {
  margin-right: 15px;
}

.country-list,
.country-info {
  padding: 0;
}

